import React, { Component } from 'react';
import { Input,Button,Popover,Modal,Form,Select,Upload } from 'antd';

import BreadcrumbContent from '../../BreadcrumbContent';
import './index.css';
const { Search, TextArea } = Input;
const { Option } = Select;

function getNewOrderNo() {
  const dateNo = new Date().getTime();
  const randNo = Math.round(Math.random()*10000);
  return ''+dateNo+randNo;
}
export default class TableCp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      addWorkorderModalVisible: false,
      delWorkorderModalVisible: false,
      uploadFileName: '未上传任何文件',
      newOrderNo:'',
      skip:0,
      pageSize:10,
      data:[{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},{},],
      dataTotal:50,
      searchKey:''
    }
  }
  getWordOrders(skip,pageSize,searchKey){
    if(searchKey!==null&&searchKey!==''){
      console.log('搜索',searchKey,skip,pageSize)
    }else{
      console.log('加载数据',skip,pageSize)
    }
  }
  componentDidMount(){
    const {skip,pageSize} = this.state;
    this.getWordOrders(skip,pageSize,null);
  }
  onSearch = (value) => {
    this.setState({skip:0,pageSize:10,searchKey:value})
    this.getWordOrders(0,10,value);
  }
  showModal = (modal) => {
    switch (modal) {
      case 'addorder': {
        this.setState({ addWorkorderModalVisible: true,newOrderNo:getNewOrderNo() });
        break;
      }
      case 'delorder':{
        this.setState({delWorkorderModalVisible: true})
        break;
      }
      default:
        break;
    }
  }
  addModalHandle = (option) => {
    if (option === 0) {
      this.addOrderFormRef.current?.resetFields();
      this.setState({ addWorkorderModalVisible: false })
    } else {
      this.addOrderFormRef.current?.submit();

    }
  }
  delModalHandle = (option)=>{
    if(option===0){
      this.setState({delWorkorderModalVisible:false})
    }else{
      this.setState({delWorkorderModalVisible:false})
    }
  }
  uploadFileHandle = () => {
    // console.log('chang file')
    // console.log(this.addOrderFormRef.current?.getFieldValue('upload').file.name)
    this.setState({uploadFileName: this.addOrderFormRef.current?.getFieldValue('upload').file.name})

  }
  loadMore = ()=>{
    const {skip,pageSize,searchKey} =this.state;
    this.setState({skip:skip+pageSize});
    this.getWordOrders(skip+pageSize,pageSize,searchKey);
  }
  addOrderFormRef = React.createRef();
  onFinish = (values) => {
    console.log(values);
    this.addOrderFormRef.current?.resetFields();
    this.setState({ addWorkorderModalVisible: false });
    return true;
  };
  render() {
    const { addWorkorderModalVisible,delWorkorderModalVisible, uploadFileName ,newOrderNo,dataTotal,data} = this.state;
    return (
      <div className="list-main animated bounceInRight">
        <BreadcrumbContent routeUrl={this.props.history.location.pathname}/>
        <h1>表格 一</h1>
        <div className="table1">
          <div className="top">
            <div className="left">
              <span className="title">全部工单</span>
              <span className="total">99</span>
            </div>
            <div className="right">
              <span className="search">
                <Search placeholder="搜索工单" style={{paddingRight:20}} onSearch={this.onSearch} enterButton />
              </span>
              <span>
                <Button type="primary" style={{height:35,borderRadius:5}} onClick={()=>this.showModal('addorder')}>新增工单</Button>
              </span>
            </div>
          </div>
          <div className="content">
            <table>
              <tbody>
              <tr style={{height:40}}>
                <td>工单编号</td>
                <td>主题-xxxxxxxxxxxxxxxxxx</td>
                <td>客户公司</td>
                <td>创建日期</td>
                <td>工单状态</td>
                <td></td>
                <td></td>
              </tr>
              <tr style={{height:40}}>
                <td>001</td>
                <td style={{fontWeight:700}}>软件学院教务数据服务平台</td>
                <td>北京传媒</td>
                <td style={{fontWeight:700}}>2021年02月25日</td>
                <td style={{color:'rgb(190, 190, 190)',paddingLeft:10}}> <span style={{width:10,height:10,verticalAlign:'middle',display:'inline-block',borderRadius:'50%',backgroundColor:"red"}}></span> 关闭</td>
                <td style={{color:'rgb(24, 144, 255)'}}>详情</td>
                <td style={{color:'rgb(24, 144, 255)'}}> 
                  <Popover placement="bottomLeft" content={<Button id='others-option-del' onClick={()=>this.showModal('delorder')}>删除</Button>} trigger="click"><span style={{cursor:'pointer'}}>. . .</span></Popover>
                </td>
              </tr>
              <tr><td colSpan="7"><hr style={{border:'1px solid rgb(234, 234, 234)'}}/></td></tr></tbody>
            </table>
          </div>
          <div className="bottom"> 
            {data.length<dataTotal?<Button id='loadmore-btn' style={{lineHeight:'1px'}} onClick={this.loadMore}>加载更多</Button>:<h1>我是有底线的...</h1>}</div>
          <div className="modal">
            <Modal
              visible={addWorkorderModalVisible}
              title="新增工单"
              onCancel={() => this.addModalHandle(0)}
              style={{ overflow: 'hidden', minWidth: '400px' }}
              footer={[
                <Button key="add" type="primary" style={{position:'relative',right:'418px'}} onClick={() => this.addModalHandle(1)}>
                  创建
                </Button>
              ]}
            >
              <Form ref={this.addOrderFormRef} layout={'vertical'} name="addOrderForm" onFinish={this.onFinish}>
                <Form.Item name="them" label="主题" rules={[{ required: true ,message:'请输入主题！'}]}>
                  <Input placeholder="请输入"/>
                </Form.Item>
                <Form.Item initialValue={newOrderNo} name="orderNo" label="工单编号" rules={[{ required: true}]}>
                  <Input  readOnly={true}/>
                </Form.Item>
                <Form.Item name="project" label="项目" rules={[{ required: true ,message:'请选择项目！'}]}>
                  <Select style={{ width: 300 }} placeholder="请选择">
                    <Option key="jack" value="jack">Jack</Option>
                    <Option key="lucy" value="lucy">Lucy</Option>
                    <Option key="Yi" value="Yiminghe">yiminghe</Option>
                  </Select>
                </Form.Item>
                <Form.Item name="customerCmp" label="客户公司" rules={[{ required: true ,message:'请选择客户公司！'}]}>
                  <Select style={{ width: 300 }} placeholder="请选择">
                    <Option key="j1" value="jack">Jack</Option>
                    <Option key="j2" value="lucy">Lucy</Option>
                    <Option key="j3" value="Yiminghe">yiminghe</Option>
                  </Select>
                </Form.Item>
                <Form.Item name="problemDec" label="问题描述" rules={[{ required: false }]}>
                  <TextArea rows={3} />
                </Form.Item>
                <Form.Item name="upload" label="上传材料" rules={[{ required: false }]}>
                  <Upload  action="https://www.mocky.io/v2/5cc8019d300000980a055e76" onChange={this.uploadFileHandle} fileList={[]} multiple={false}>
                    <Button type="primary" style={{width:'100px',borderRadius:'5px'}}>上传</Button> 您上传的文件名为 : {uploadFileName}
                  </Upload>
                </Form.Item>
              </Form>
            </Modal>
            <Modal
              visible={delWorkorderModalVisible}
              title="系统提示"
              onOk={()=>this.delModalHandle(1)}
              onCancel={() => this.delModalHandle(0)}
              okText="确认" cancelText="取消"
            >你确定要删除该工单吗？</Modal>
          </div>
        </div>
      </div>  
    )
  }
}
