import React, { Component } from 'react'
import { Route } from 'react-router-dom'
import { Layout, Menu } from 'antd';
import { LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
//多色图标
import { SmileTwoTone, HeartTwoTone, CheckCircleTwoTone } from '@ant-design/icons';
import { GoldOutlined } from '@ant-design/icons';
import { withRouter } from 'react-router-dom';
import ButtonCp from '../../components/Components/ButtonCp'
import TableCp from '../../components/Components/TableCp';
import ReduxCp from '../../components/ReduxCp'
import './index.css'

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;
class Main extends Component {
  constructor(props) {
    super(props)
    this.state = {

    }
  }

  routeSkip = (routeUrl) => {
    this.props.history.push(routeUrl);
  }

  render() {
    const LeftNav = {
      Components: [
        { title: 'Button', routeUrl: '/app/components/button' },
        { title: 'Table', routeUrl: '/app/components/table' },
        { title: 'Button3', routeUrl: '/app/components/button2' },
        { title: 'Button4', routeUrl: '/app/components/button3' }],
      Hooks: [
        { title: 'Hook', routeUrl: '/app/components/button5' },
        { title: 'Hook2', routeUrl: '/app/components/button6' },
        { title: 'Hook3', routeUrl: '/app/components/button7' },
        { title: 'Hook4', routeUrl: '/app/components/button9' }
      ],
      Redux: [
        { title: 'Basics', routeUrl: '/app/redux/basics' },
        { title: 'Redux2', routeUrl: '/app/components/button11' },
        { title: 'Redux3', routeUrl: '/app/components/button12' },
        { title: 'Redux4', routeUrl: '/app/components/button13' }
      ]
    }
    const currentRoute = this.props.history.location.pathname
    const currentPage = currentRoute.split('/')[2]
    return (
      <div id="main">
        <Layout>
          <Header className="header animatedTop bounceInDown" theme="light">
            <div className="logo" >
              <SmileTwoTone />
              <HeartTwoTone twoToneColor="#eb2f96" />
              <CheckCircleTwoTone twoToneColor="#52c41a" />
            </div>
            <Menu theme="light" mode="horizontal" defaultSelectedKeys={['2']}>
              <Menu.Item key="1">设计</Menu.Item>
              <Menu.Item key="2">文档</Menu.Item>
              <Menu.Item key="3">Gitee</Menu.Item>
            </Menu>
          </Header>
          <Layout>
            <Sider width={200} className="site-layout-background animatedLeftNav bounceInLeft ">
              <Menu
                mode="inline"
                defaultSelectedKeys={[currentRoute]}
                defaultOpenKeys={[currentPage]}
                style={{ height: '100%', borderRight: 0 }}
              >
                <SubMenu key="components" icon={<GoldOutlined />} title="Components">
                  {
                    LeftNav.Components.map((item) => {
                      return (<Menu.Item key={item.routeUrl} onClick={() => this.routeSkip(item.routeUrl)}>{item.title}</Menu.Item>)
                    })
                  }
                </SubMenu>
                <SubMenu key="hooks" icon={<LaptopOutlined />} title="Hook">
                  {
                    LeftNav.Hooks.map((item) => {
                      return (<Menu.Item key={item.routeUrl} onClick={() => this.routeSkip(item.routeUrl)}>{item.title}</Menu.Item>)
                    })
                  }
                </SubMenu>
                <SubMenu key="redux" icon={<NotificationOutlined />} title="Redux">
                  {
                    LeftNav.Redux.map((item) => {
                      return (<Menu.Item key={item.routeUrl} onClick={() => this.routeSkip(item.routeUrl)}>{item.title}</Menu.Item>)
                    })
                  }
                </SubMenu>
              </Menu>
            </Sider>
            <Layout style={{ padding: '0 24px 24px' }}>
              <Content
                className="site-layout-background"
                style={{
                  margin: 0,
                  minHeight: 280,
                }}
              >
                <Route path="/app/components/button" component={ButtonCp}></Route>
                <Route path="/app/components/table" component={TableCp}></Route>
                <Route path="/app/redux/basics" component={ReduxCp}></Route>
              </Content>
            </Layout>
          </Layout>
        </Layout>
      </div>
    )
  }
}

export default withRouter(Main);

