import React, { Component } from 'react'
import {Redirect, Route} from 'react-router-dom'
import { Breadcrumb } from 'antd';
import Basics from './component/Basics'

export default class index extends Component {
  render() {
    const routeUrl = this.props.history.location.pathname;
    const routeList = routeUrl.split('/').filter((item)=>item!=='');
    return (
      <div className="main animated bounceInRight">
        <Breadcrumb style={{ margin: '16px 0' }}>
          {
            routeList.map((item,index)=>{return(<Breadcrumb.Item key={index}>{item.substring(0,1).toUpperCase()+item.substring(1)}</Breadcrumb.Item>)})
          }
        </Breadcrumb>
        <div className='main'>
          <h1>Redux</h1>
          <Route path="/app/redux/basics" component={Basics}></Route>
          <Redirect to="/app/redux/basics"></Redirect>
        </div>
      </div>
    )
  }
}
